
@charset "UTF-8";
/*
 * Description: 登录、注册页、找回密码等表单页面样式
 * User: zhaoyiming
 * Date: 2017/9/4
*/
.account-check {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
}
.account-check .account-check-btn {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  height: 50px;
  cursor: pointer;
}
.account-check .account-check-btn span {
  display: block;
  position: absolute;
  width: 100px;
  height: 32px;
  left: 50%;
  top: 8px;
  margin-left: -50px;
  line-height: 32px;
  text-align: center;
  font-size: 15px;
  color: #b4b4b4;
}
.account-check .account-check-btn.this-account-model span {
  border-bottom: 2px solid #ffb000;
  color: #4c4c4c;
}
.account-container {
  width: 84%;
  padding: 20px 8% 0 8%;
}
.account-container-form p {
  position: relative;
  margin-bottom: 15px;
}
.phone,
.code,
.pwd,
.account {
  height: 45px;
  border-radius: 3px;
  background-color: #f2f2f2;
  font-size: 14px;
  color: #4d4d4d;
}
.phone-ico,
.code-ico,
.phone-ico,
.pwd-ico {
  display: block;
  position: absolute;
  height: 100%;
  background-size: 30px 30px;
}
.phone-ico {
  background-image: url("images/phone.png");
  background-repeat: no-repeat;
  background-position: 0 7px;
}
.code-ico {
  background-image: url("images/verify.png");
  background-repeat: no-repeat;
  background-position: 0 7px;
}
.phone-ico {
  background-image: url("images/account.png");
  background-repeat: no-repeat;
  background-position: 0 7px;
}
.pwd-ico {
  background-image: url("images/password.png");
  background-repeat: no-repeat;
  background-position: 0 7px;
}
.get-code {
  position: absolute;
  right: 10px;
  top: 8px;
  width: 90px;
  height: 30px;
  background-color: #fff;
  font-size: 13px;
}
.get-code-on {
  color: #b4b4b4;
}
.get-code-off {
  color: #2577e3;
}
.account-btn {
  display: block;
  width: 100%;
  height: 45px;
  margin: 0 0 20px 0;
  text-align: center;
  line-height: 45px;
  border-radius: 25px;
  background-color: #2577e3;
  color: #fff;
  font-size: 15px;
}
.unable {
  display: block;
  text-align: right;
  color: #2577e3;
  font-size: 12px;
}
.phone-prompt {
  display: block;
  text-align: right;
  color: #afafaf;
  font-size: 12px;
}
@media only screen and (max-width: 319px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 11%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 89%;
    padding-left: 11%;
}
}
@media only screen and (min-width: 320px) and (max-width: 374px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 11%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 89%;
    padding-left: 11%;
}
}
@media only screen and (min-width: 375px) and (max-width: 413px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 10%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 90%;
    padding-left: 10%;
}
}
@media only screen and (min-width: 414px) and (max-width: 480px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 9%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 91%;
    padding-left: 9%;
}
}
@media only screen and (min-width: 480px) and (max-width: 767px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 7%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 93%;
    padding-left: 7%;
}
}
@media only screen and (min-width: 768px) {
.phone-ico,
  .code-ico,
  .pwd-ico,
  .phone-ico {
    width: 6%;
}
.phone,
  .code,
  .pwd,
  .account {
    width: 94%;
    padding-left: 6%;
}
}



